import io, { Socket } from 'socket.io-client';
import Chatty, { ChattyException } from '.';
import {
  ChatType,
  ChattyEvent,
  ErrorResponseType,
  ChatListFetchOptions,
  ChatListFetchResponseType,
  ChatListFetchHandler,
  ChatListRefreshHandler,
  ChatListConnectHandler,
  ChatListConnectResponseType
} from './Types';

declare const MODE: string;

const dev = MODE === 'development' ? 'dev' : '';

export class ChatList {
  socket: Socket | null;

  connectHandler: ChatListConnectHandler | null;
  fetchChatListHandler: ChatListFetchHandler | null;
  refreshChatListHandler: ChatListRefreshHandler | null;

  constructor() {
    this.socket = null;
    this.connectHandler = null;
    this.fetchChatListHandler = null;
    this.refreshChatListHandler = null;
  }

  onConnectHandler(handler: ChatListConnectHandler) {
    this.connectHandler = handler;
  }
  onFetchChatListHandler(handler: ChatListFetchHandler) {
    this.fetchChatListHandler = handler;
  }
  onRefreshChatListHandler(handler: ChatListRefreshHandler) {
    this.refreshChatListHandler = handler;
  }

  connect() {
    if (!Chatty.app || !Chatty.member) {
      throw new ChattyException('E2001');
    }

    const url: string = MODE === 'none' ? 'ws://localhost:4000' : `wss://${dev}socket.chatty-cloud.com`;
    this.socket = io(`${url}/chatlist.${Chatty.app.name}`, {
      transports: ["polling", "websocket"],
      query: { MemberId: Chatty.member.id! },
      auth: {
        token: Chatty.apiKey
      }
    });
    this.addListener();
  }


  disconnect() {
    if (!this.socket) {
      throw new ChattyException('E4003');
    }

    this.socket.disconnect();
    this.removeListener();
    this.socket = null;
    console.debug('::: Chatty DISCONNECT_DONE ChatList');
  }


  fetchChatList(options: ChatListFetchOptions) {
    if (!this.socket) {
      throw new ChattyException('E4003');
    }
    this.socket.emit(ChattyEvent.FETCH_CHATLIST, options);
  }


  refreshChatList(ChatId: string) {
    if (!this.socket) {
      throw new ChattyException('E4003');
    }
    this.socket.emit(ChattyEvent.REFRESH_CHATLIST, ChatId);
  }


  addListener() {
    if (!this.socket) {
      throw new ChattyException('E4003');
    }


    this.socket.on(ChattyEvent.CONNECT_DONE, (data: ChatListConnectResponseType) => {
      if (!this.connectHandler) {
        throw new ChattyException('E4005', 'connectHandler');
      }
      console.debug('::: Chatty CONNECT_DONE ChatList');
      this.connectHandler(data, null);
    });
    this.socket.on(ChattyEvent.CONNECT_FAIL, (err: ErrorResponseType) => {
      if (!this.connectHandler) {
        throw new ChattyException('E4005', 'connectHandler');
      }
      console.debug('::: Chatty CONNECT_FAIL', err);
      this.connectHandler(null, err);
    });


    this.socket.on(ChattyEvent.FETCH_CHATLIST_DONE, (chats: ChatListFetchResponseType) => {
      if (!this.fetchChatListHandler) {
        throw new ChattyException('E4005', 'fetchChatListHandler');
      }
      console.debug('::: Chatty FETCH_CHATLIST_DONE', chats);
      this.fetchChatListHandler(chats, null);
    });
    this.socket.on(ChattyEvent.FETCH_CHATLIST_FAIL, (err: ErrorResponseType) => {
      if (!this.fetchChatListHandler) {
        throw new ChattyException('E4005', 'fetchChatListHandler');
      }
      console.debug('::: Chatty FETCH_CHATLIST_FAIL', err);
      this.fetchChatListHandler(null, err);
    });


    this.socket.on(ChattyEvent.REFRESH_CHATLIST_DONE, (chat: ChatType) => {
      if (!this.refreshChatListHandler) {
        throw new ChattyException('E4005', 'refreshChatListHandler');
      }
      console.debug(`::: Chatty REFRESH_CHATLIST_DONE`, chat);
      this.refreshChatListHandler(chat, null);
    });
    this.socket.on(ChattyEvent.REFRESH_CHATLIST_FAIL, (err: ErrorResponseType) => {
      if (!this.refreshChatListHandler) {
        throw new ChattyException('E4005', 'refreshChatListHandler');
      }
      console.debug(`::: Chatty REFRESH_CHATLIST_FAIL`, err);
      this.refreshChatListHandler(null, err);
    });
  }


  removeListener() {
    if (!this.socket) {
      throw new ChattyException('E4003');
    }

    this.socket.off(ChattyEvent.CONNECT_DONE);
    this.socket.off(ChattyEvent.CONNECT_FAIL);

    this.socket.off(ChattyEvent.FETCH_CHATLIST_DONE);
    this.socket.off(ChattyEvent.FETCH_CHATLIST_FAIL);

    this.socket.off(ChattyEvent.REFRESH_CHATLIST_DONE);
    this.socket.off(ChattyEvent.REFRESH_CHATLIST_FAIL);
  }
}