export type AppType = {
  id: string
  name: string
  server: string
  apiKey: string
  firebaseCredentials: FirebaseCredentialsType | null
  state: AppStateType
  pricing: PricingType
  image: string
  language: string
  country: string
  enableTranslation: boolean
  chatPageLimit: number
  chatListPageLimit: number
  thumbnailSize: number
  systemMessages: Array<SystemMessagesIndexSignature> | []
  notificationSound: string
  enableImageUpload: boolean
  enableVideoUpload: boolean
  maxImageSize: number
  maxVideoSize: number
  multipleUploadCount: number
  createdAt: Date
  updatedAt: Date
  deletedAt: Date | null
}

export type AppUserType = {
  role: AppRoleType
  createdAt: Date
  updatedAt: Date
  AppId: string
  UserId: string
}

export type UserType = {
  id: string
  email: string
  password: string
  name: string
  avatar: string
  verification: boolean
  tfaEnable: boolean
  token: string
  tfa: string
  visitedAt: Date
  createdAt: Date
  updatedAt: Date
  deletedAt: Date | null
}

export type ChatType = {
  id: string
  private: boolean
  open: boolean
  name: string
  image: string
  lastMessage: MessageType | null
  data: object | null
  distinctKey: string
  group: string
  maxMember: number
  createdAt: Date
  updatedAt: Date
  deletedAt: Date | null
  AppId: string
}

export type ChatMemberType = {
  missedCount: number
  role: ChatRoleType
  createdAt: Date
  updatedAt: Date
  ChatId: string
  MemberId: string
}

export type MemberType = {
  id: string
  name: string
  language: string
  country: string
  avatar: string
  deviceToken: string
  device: DeviceType | null
  group: string
  permission: MemberPermissionType
  data: object | null
  createdAt: Date
  updatedAt: Date
  deletedAt: Date | null
  AppId: string
}

export type MessageType = {
  id: string
  text: string | null
  files: Array<{ uri: string }> | null
  json: object | null
  type: MessageFormatType
  translation: TranslationIndexSignature | null
  by: ByType
  readReceipt: number
  createdAt: Date
  updatedAt: Date
  deletedAt: Date | null
  ChatId: string
  SenderId: string | null
}

export type InvitationType = {
  role: string
  accept: boolean
  email: string
  inviter: string
  createdAt: Date
  updatedAt: Date
  AppId: string
}


