export type DeviceType = {
  platform: string,
  language: string,
  product: string,
  userAgent: string,
  sdkVersion: string,
};

export enum MemberPermissionType {
  NONE = 0,
  READ,
  WRITE,
  ADMIN = 5,
  SUPER = 10
};

export type FileType = {
  uri: string,
  type: string,
  name: string
}

export type FirebaseCredentialsType = {
  privateKey: string,
  clientEmail: string,
  projectId: string,
}

export type ByType = 'USER' | 'ADMIN' | 'SYSTEM';
export type PricingType = 'FREE' | 'PRODUCTION' | 'ADVANCED' | 'ENTERPRISE';
export type AppStateType = 'ACTIVE' | 'DEACTIVE';
export type AppRoleType = 'OWNER' | 'DEVELOPER' | 'MANAGER';
export type ChatRoleType = 'OWNER' | 'MEMBER' | 'OBSERVER';
export type MessageFormatType = 'TEXT' | 'FILE' | 'JSON';

export interface SystemMessagesIndexSignature {
  [key: string]: string
}

export interface TranslationIndexSignature {
  [key: string]: string
}

/**
 * Default System Message
 * string empty  system message created  chat Member silent push  
 * invite  exclude   push  .
 * join  leave    chat member  push 
 */
export const DefaultSystemMessages = [
  { welcome: 'Welcome to chat' },
  { invite: '{MEMBER-NAME} was invited to chat' },
  { exclude: '{MEMBER-NAME}  was excluded from chat' },
  { join: '{MEMBER-NAME}  joined chat' },
  { leave: '{MEMBER-NAME}  left chat' }
];

export const SupportedImageFormat = ['image/png', 'image/jpg', 'image/jpeg', 'image/gif', 'image/webp'];
export const SupportedVideoFormat = ['video/mp4', 'video/webm'];


export interface ChatConnectOptions {
  at?: string | undefined;
  with?: string | string[] | undefined;
  distinctKey?: string | null | undefined;
  private?: boolean | undefined;
  name?: string | undefined;
  image?: string | undefined;
  data?: {} | undefined;
  group?: string | undefined;
}

export interface ChatListFetchOptions {
  refresh: boolean | undefined;
  group: string | undefined;
}

export type MessagesFetchResponseType = {
  refresh: boolean;
  hasNext: boolean;
  messages: Array<MessageType>;
} | null

export type ChatConnectResponseType = ChatType | null;
export type ChatListConnectResponseType = {} | null;
export type MessageSendResponseType = MessageType | null;
export type MessageReceiveResponseType = MessageType | null;
export type MessagesUpdateResponseType = Array<MessageType> | null;
export type ChatListRefreshResponseType = ChatType | null;
export type ChatRefreshResponseType = ChatType | null;

export type ChatListFetchResponseType = {
  refresh: boolean;
  hasNext: boolean;
  chats: Array<ChatType>;
} | null

export type ErrorResponseType = {
  message: string;
} | null;

export declare type ChatConnectHandler = (res: ChatConnectResponseType, err: ErrorResponseType) => void;
export declare type ChatListConnectHandler = (res: ChatListConnectResponseType, err: ErrorResponseType) => void;
export declare type MessageSendHandler = (res: MessageSendResponseType, err: ErrorResponseType) => void;
export declare type MessagesFetchHandler = (res: MessagesFetchResponseType, err: ErrorResponseType) => void;
export declare type ChatListFetchHandler = (res: ChatListFetchResponseType, err: ErrorResponseType) => void;
export declare type MessageReceiveHandler = (res: MessageReceiveResponseType, err: ErrorResponseType) => void;
export declare type MessagesUpdateHandler = (res: MessagesUpdateResponseType, err: ErrorResponseType) => void;
export declare type ChatListRefreshHandler = (res: ChatListRefreshResponseType, err: ErrorResponseType) => void;
export declare type ChatRefreshHandler = (res: ChatRefreshResponseType, err: ErrorResponseType) => void;


