import io, { Socket } from 'socket.io-client';
import Chatty, { ChattyException } from '.';
import { ChatList } from './ChatList';
import {
  ChatType,
  MessageType,
  ChattyEvent,
  ErrorResponseType,
  MessagesFetchResponseType,
  ChatConnectOptions,
  MessagesFetchHandler,
  MessageReceiveHandler,
  MessagesUpdateHandler,
  ChatRefreshHandler,
  MessageSendHandler,
  ChatConnectHandler
} from './Types';

declare const MODE: string;

const dev = MODE === 'development' ? 'dev' : '';

export class Chat {
  socket: Socket | null;
  chatlist: ChatList | null;
  id: string | null;

  connectHandler: ChatConnectHandler | null;
  sendMessageHandler: MessageSendHandler | null;
  fetchMessagesHandler: MessagesFetchHandler | null;
  newMessageHandler: MessageReceiveHandler | null;
  updateMessagesHandler: MessagesUpdateHandler | null;
  refreshChatHandler: ChatRefreshHandler | null;

  constructor() {
    this.socket = null;
    this.chatlist = null;
    this.id = null;
    this.connectHandler = null;
    this.sendMessageHandler = null;
    this.fetchMessagesHandler = null;
    this.newMessageHandler = null;
    this.updateMessagesHandler = null;
    this.refreshChatHandler = null;
  }


  onConnectHandler(handler: ChatConnectHandler) {
    this.connectHandler = handler;
  }
  onSendMessageHandler(handler: MessageSendHandler) {
    this.sendMessageHandler = handler;
  }
  onFetchMessagesHandler(handler: MessagesFetchHandler) {
    this.fetchMessagesHandler = handler;
  }
  onNewMessageHandler(handler: MessageReceiveHandler) {
    this.newMessageHandler = handler;
  }
  onUpdateMessagesHandler(handler: MessagesUpdateHandler) {
    this.updateMessagesHandler = handler;
  }
  onRefreshChatHandler(handler: ChatRefreshHandler) {
    this.refreshChatHandler = handler;
  }

  connect(options: ChatConnectOptions, chatlist: ChatList | null) {
    if (!Chatty.app || !Chatty.member || !Chatty.member.id) {
      throw new ChattyException('E2001');
    }

    if (!options.at && !options.with) {
      throw new ChattyException('E4007');
    }

    if (options.with) {
      if (typeof options.with === 'string') {
        options.with = [options.with];

      }
      options.with = options.with.filter(MemberId => MemberId && MemberId !== 'undefined' && MemberId !== 'null')
      options.with.push(Chatty.member.id);

      // Remove duplicated elements
      options.with = options.with.filter((value, index, self) => self.indexOf(value) === index);

      if (options.with.length < 2) {
        throw new ChattyException('E4008');
      }

      if (options.distinctKey === undefined) {
        options.distinctKey = Chatty.generateDistinctKey(options.with)
      }
    }


    const url: string = MODE === 'none' ? 'ws://localhost:4000' : `wss://${dev}socket.chatty-cloud.com`;

    // option checker
    Object.keys(options).forEach(key => !options[key] && delete options[key]);

    this.socket = io(`${url}/chat.${Chatty.app.name}`, {
      transports: ["polling", "websocket"],
      query: {
        MemberId: Chatty.member.id!,
        options: JSON.stringify(options)
      },
      auth: {
        token: Chatty.apiKey
      },
      forceNew: true,
      // withCredentials: true, // cors "*"   .
    });

    if (chatlist) {
      this.chatlist = chatlist;
    }

    this.addListener();
  }

  disconnect() {
    if (!this.socket) {
      throw new ChattyException('E4004');
    }
    this.socket.disconnect();
    this.removeListener();
    this.socket = null;
    console.debug('::: Chatty DISCONNECT_DONE Chat');
  }
  fetchMessages(refresh: boolean) {
    if (!this.socket) {
      throw new ChattyException('E4004');
    }
    this.socket.emit(ChattyEvent.FETCH_MESSAGES, { refresh: refresh });
  }
  sendMessage(data) {
    if (!this.socket) {
      throw new ChattyException('E4004');
    }
    this.socket.emit(ChattyEvent.SEND_MESSAGE, { ...data, retry: 5 });
  }
  refreshChat(ChatId: string) {
    if (!this.socket) {
      throw new ChattyException('E4004');
    }

    // chat       
    this.socket.emit(ChattyEvent.REFRESH_CHAT, ChatId);
  }
  markAsRead() {
    if (!this.socket) {
      throw new ChattyException('E4004');
    }
    this.socket.emit(ChattyEvent.MARK_AS_READ);
  }






  addListener() {
    if (!this.socket) {
      throw new ChattyException('E4004');
    }

    this.socket.on(ChattyEvent.CONNECT_DONE, (chat: ChatType) => {
      if (!this.connectHandler) {
        throw new ChattyException('E4006', 'connectHandler');
      }
      console.debug(`::: Chatty CONNECT_DONE`, chat);
      this.id = chat.id; //  ChatId Chat instance 
      this.connectHandler(chat, null);
    });

    this.socket.on(ChattyEvent.CONNECT_FAIL, (err: ErrorResponseType) => {
      if (!this.connectHandler) {
        throw new ChattyException('E4006', 'connectHandler');
      }
      console.warn('::: Chatty CONNECT_FAIL', err);
      this.connectHandler(null, err);
    });

    this.socket.on(ChattyEvent.FETCH_MESSAGES_DONE, (data: MessagesFetchResponseType) => {
      if (!this.fetchMessagesHandler) {
        throw new ChattyException('E4006', 'fetchMessagesHandler');
      }
      console.debug('::: Chatty FETCH_MESSAGES_DONE', data);
      this.fetchMessagesHandler(data, null);

      this.markAsRead();
    });

    this.socket.on(ChattyEvent.FETCH_MESSAGES_FAIL, (err: ErrorResponseType) => {
      if (!this.fetchMessagesHandler) {
        throw new ChattyException('E4006', 'fetchMessagesHandler');
      }
      console.warn('::: Chatty FETCH_MESSAGES_FAIL', err);
      this.fetchMessagesHandler(null, err);
    });

    this.socket.on(ChattyEvent.SEND_MESSAGE_DONE, (message) => {
      if (!this.sendMessageHandler) {
        throw new ChattyException('E4006', 'sendMessageHandler');
      }
      console.debug('::: Chatty SEND_MESSAGE_DONE', message);
      this.sendMessageHandler(message, null);

      //       ChatList .
      if (this.chatlist?.socket) {
        this.chatlist.refreshChatList(message.ChatId);
      }
    });
    this.socket.on(ChattyEvent.SEND_MESSAGE_FAIL, (err) => {
      if (!this.sendMessageHandler) {
        throw new ChattyException('E4006', 'sendMessageHandler');
      }
      console.warn('::: Chatty SEND_MESSAGE_FAIL', err);
      this.sendMessageHandler(null, err);
    });

    this.socket.on(ChattyEvent.SEND_MESSAGE_RETRY, (data) => {
      if (!this.socket) {
        throw new ChattyException('E4004');
      }
      console.debug('::: Chatty SEND_MESSAGE_RETRY', data);
      this.socket.emit(ChattyEvent.SEND_MESSAGE, data);
    });

    this.socket.on(ChattyEvent.NEW_MESSAGE, (message: MessageType) => {
      if (!this.newMessageHandler) {
        throw new ChattyException('E4006', 'newMessageHandler');
      }
      console.debug('::: Chatty NEW_MESSAGE', message);
      this.newMessageHandler(message, null);

      this.markAsRead();
    });
    this.socket.on(ChattyEvent.MARK_AS_READ_DONE, (data: any) => {
      console.debug('::: Chatty MARK_AS_READ_DONE');

      // ChatList  ChatList .
      if (this.chatlist?.socket) {
        this.chatlist.refreshChatList(data.ChatId);
      }
    });
    this.socket.on(ChattyEvent.MARK_AS_READ_FAIL, (err: ErrorResponseType) => {
      console.warn('::: Chatty MARK_AS_READ_FAIL', err);
    });
    this.socket.on(ChattyEvent.MARK_AS_READ_BYPASS, () => {
      // MARK_AS_READ_DONE   data MARK_AS_READ_BYPASS  . 
      // member  SUPER  MARK_AS_READ   bypass 
      console.debug('::: Chatty MARK_AS_READ_BYPASS');
    });

    /**
     * UPDATE_MESSAGES    
     * server   MARK_AS_READ_DONE  
     * MARK_AS_READ   
     * 1.  Send Message  NEW_MESSAGE  
     * 2.  Chat   FETCH_MESSAGES 
     */
    this.socket.on(ChattyEvent.UPDATE_MESSAGES, (messages) => {
      if (!this.updateMessagesHandler) {
        throw new ChattyException('E4006', 'updateMessagesHandler');
      }
      console.debug('::: Chatty UPDATE_MESSAGES');
      this.updateMessagesHandler(messages, null);
    });

    this.socket.on(ChattyEvent.REFRESH_CHAT_DONE, (chat: ChatType) => {
      if (!this.refreshChatHandler) {
        throw new ChattyException('E4006', 'refreshChatHandler');
      }
      console.debug(`::: Chatty REFRESH_CHAT_DONE`, chat);
      this.refreshChatHandler(chat, null);
    });
    this.socket.on(ChattyEvent.REFRESH_CHAT_FAIL, (err: ErrorResponseType) => {
      if (!this.refreshChatHandler) {
        throw new ChattyException('E4006', 'refreshChatHandler');
      }
      console.warn(`::: Chatty REFRESH_CHAT_FAIL`, err);
      this.refreshChatHandler(null, err);
    });
  }



  removeListener() {
    if (!this.socket) {
      throw new ChattyException('E4004');
    }

    this.socket.off(ChattyEvent.CONNECT_DONE);
    this.socket.off(ChattyEvent.CONNECT_FAIL);

    this.socket.off(ChattyEvent.FETCH_MESSAGES_DONE);
    this.socket.off(ChattyEvent.FETCH_MESSAGES_FAIL);

    this.socket.off(ChattyEvent.SEND_MESSAGE_DONE);
    this.socket.off(ChattyEvent.SEND_MESSAGE_FAIL);

    this.socket.off(ChattyEvent.SEND_MESSAGE_RETRY);

    this.socket.off(ChattyEvent.NEW_MESSAGE);

    this.socket.off(ChattyEvent.MARK_AS_READ_DONE);
    this.socket.off(ChattyEvent.MARK_AS_READ_FAIL);
    this.socket.off(ChattyEvent.MARK_AS_READ_BYPASS);

    this.socket.off(ChattyEvent.REFRESH_CHAT_DONE);
    this.socket.off(ChattyEvent.REFRESH_CHAT_FAIL);

  }

}